'use strict';

import React from 'react';

require('../base.less');
require('./ver.less');

// mock data
import mockData from '../getMock';
let mockData2 = mockData.concat(mockData);
mockData2 = mockData2.concat(mockData2);

class VerComponent extends React.Component {

  constructor(props) {
    super(props);
  
    this.state = {};
  }

  componentDidMount() {
    let data = mockData2;
    let programList = this.props.programList;
    if (programList && programList.length > 0) {
      data = programList;
    }
    let html = this.getTemplateHtml();
    this.refs.temp.innerHTML += html;
    this.root = FocusEngine.render(document.body, true);
    this.grid = this.root.getWidgetById('grid');
    this.grid.setItemRender(this.renderGridItem);
    this.grid.setData(data);
    this.bindGrid();
    document.getElementById('grid').style.paddingTop = '3.3rem';
  }

  renderGridItem(dom, item) {
    if (item && item.picUrl) {
      dom.querySelector('.item-title').textContent = item.name;
      dom.querySelector('.poster').setAttribute('src', item.picUrl);
    }
    // 如果不存在item，要将dom的内容置为空的状态，保证在滚动时不会有内容保留
    else {
      dom.querySelector('.item-title').textContent = '';
      dom.querySelector('.poster').setAttribute('src', '');
    }
  }

  bindGrid() {
    this.grid.on('focus', e => {
      document.getElementById('grid').style.transition = 'padding-top .2s ease-out';
      // 纵向Grid时，通过whichRow获取当前该widget对应的是第几行数据
      var row = e.target.whichDataRow;
      if (row >= 1) {
        document.getElementById('grid').style.paddingTop = 0;
      } else {
        document.getElementById('grid').style.paddingTop = '3.3rem';
      }
    });
  }

  /**
   * 禁止浏览器默认处理keydown事件
   */
  preventDefaultKeyDownEvent() {
    document.addEventListener('keydown', this.keydownEventHandler);
  }

  /**
   * 恢复浏览器默认处理keydown事件
   */
  revertDefaultKeyDownEvent() {
    document.removeEventListener('keydown', this.keydownEventHandler);
  }

  /**
   * keydown event handler
   */
  keydownEventHandler(e) {
    console.log('e.keyCode: ' + e.keyCode);
    e.preventDefault();
  }

  getTemplateHtml() {
    return `
      <div id="grid" class="grid" fe-role="Grid" fe-cfg="grid_row:3,grid_col:6,grid_duration:0.3,grid_delay:0.01">
          <div class="grid-item" fe-role="Widget">
            <div class="item-content">
              <img src="//gw.alicdn.com/tps/TB1I1YiLpXXXXaEXVXXXXXXXXXX-309-201.png_.webp" class="poster" />
              <span class="item-title marquee"></span>
              </div>
            </div>
          </div>
        </div>
    `;
  }

  render() {
    const {bgImg} = this.props;
    return (
      <div ref="temp" className="ver-component pre-template">
        <img className="bg-img" src={bgImg} alt=""/>
      </div>
    );
  }

}

VerComponent.displayName = 'VerComponent';

export default VerComponent;



