'use strict';

import './upDownload.less';
import { Upload, Button, Icon } from 'antd';

const UpDownload = React.createClass({
  getInitialState() {
    return {
      fileList: [{
        uid: -1,
        name: '下载excel表格',
        status: 'done',
        url: 'http://galitv.alicdn.com/tvbase/tag/topicrule/topic_rule_program_parse_template.xlsx'
      }]
    };
  },
  handleChange(info) {
    console.log('file upload info: ', info);
    let file = info.file;
    let fileList = info.fileList;

    // 1. Limit the number of uploaded files
    //    Only to show two recent uploaded files, and old ones will be replaced by the new
    fileList = fileList.slice(-2);
    if (file.response && !file.ProgressEvent) {
      file.response && this.props.onUpload(file.response.data);
      fileList = fileList.slice(0, 1);
    }

    // 2. read from response and show file link
    // fileList = fileList.map((file) => {
    //   if (file.response) {
    //     // Component will show file.url as link
    //     file.url = file.response.url;
    //   }
    //   return file;
    // });

    // // 3. filter successfully uploaded files according to response from server
    // fileList = fileList.filter((file) => {
    //   if (file.response) {
    //     return file.response.status === 'success';
    //   }
    //   return true;
    // });

    this.setState({ fileList });
  },
  render() {
    const props = {
      action: '/topic/rule/parseProgramExcel.htm',
      onChange: this.handleChange,
      multiple: true
    };
    return (
      <div className="up-download">
        <Upload {...props} fileList={this.state.fileList}>
          <Button type="ghost">
            <Icon type="upload" /> 上传excel表格
          </Button>
        </Upload>
      </div>
    );
  }
});

export default UpDownload;



