'use strict';

import './TopicCreateForm.less';
import { Form, Radio, Button, Modal, message} from 'antd';
import UploadImg from '../uploadImg/UploadImg';
import RequestManager from 'components/request-manager/request-manager';
import globalData from '../../mock/global';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const formData = window.form || globalData;

// 专题模板图片列表
const imgList = [
  'https://img.alicdn.com/tps/TB1eYp.OFXXXXciXVXXXXXXXXXX-1920-1080.png',
  'https://img.alicdn.com/tps/TB1K91KOFXXXXXdXpXXXXXXXXXX-1920-1080.png',
  'https://img.alicdn.com/tps/TB1EtKaOFXXXXXgaXXXXXXXXXXX-1920-1080.png',
  'https://img.alicdn.com/tps/TB1XnGlOFXXXXayXVXXXXXXXXXX-1920-1080.png'
];

const TagSelectForm = Form.create()(React.createClass({
  getInitialState() {
    return {
      imgIndex: 0,
      hasErr: false,
      themeArr: [],
      themeValues: null
    };
  },

  componentDidMount() {
    // todo: 获取主题适用场景列表，并渲染
    this.getData();
  },

  getData() {
    let themeArr = [];
    let topicTemplateMap = formData.topicTemplateMap;
    let index = 0;
    for (let key in topicTemplateMap) {
      let item = {
        key: key,
        value: index,
        desc: topicTemplateMap[key],
        img: imgList[index]
      };
      themeArr.push(item);
      index ++;
    }
    this.setState({
      themeArr: themeArr
    });
  },

  handleSubmit(e) {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if (!values.bgImg) {
          values.bgImg = this.state.bgImg;
        }
        // 节能环保模板的背景图可以不用上传
        if (!values.bgImg && values.topicTemplate != 1) {
          this.setState({hasErr: true});
          return;
        } else {
          this.setState({hasErr: false});
        }
        values.topicTemplate = this.state.themeArr[values.topicTemplate].key;
        console.log('Received values of form: ', values);
        let oldValues;
        // todo: 拼接上一步的数据
        if (Object.getOwnPropertyNames(this.props.values).length > 0) {
          this.setState({themeValues: this.props.values});
          oldValues = this.props.values;
        } else {
          oldValues = this.state.themeValues;
        }
        console.log('this.props.values: ', this.props.values);
        console.log('this.state.themeValues', this.state.themeValues);
        console.log('concated data: ', {...oldValues, ...values});

        // 未创建过主题，此时调用创建主题接口
        if (formData.ruleId <= 0) {
          // 显示loading
          message.loading('创建主题中', 10);
          // todo: 提交数据创建主题, 和上一页同一个接口哦
          RequestManager.postNewTheme(oldValues)
          .then(data => {
            console.log('post new theme data return: ', data);
            if (data.success == true) {
              message.destroy();
              console.log('post new theme data success!');
              let nextData = {};
              nextData.ruleId = data.data.ruleId;
              nextData.topicTemplate = values.topicTemplate;
              nextData.topicBgUrl = values.bgImg;
              nextData.programList = data.data.programList;
              // 如果 programList 为空，给出提示
              if (nextData.programList.length == 0) {
                Modal.warning({
                  title: '警告',
                  content: '所选标签对应节目列表为空，请到上个页面修改'
                });
                return;
              }
              this.handleNext(nextData);
            } else {
              // 给出错误信息
              Modal.error({
                title: '创建主题出错',
                content: data.message + '，请到上一步修改'
              });
            }
            message.destroy();
          })
        } else {
          // 显示loading
          message.loading('保存主题中', 10);
          // 创建过主题，此时调用编辑主题接口
          RequestManager.postEditTheme(oldValues)
          .then(data => {
            console.log('post edit theme data return: ', data);
            if (data.success == true) {
              message.destroy();
              console.log('post edit theme data success!');
              let nextData = {};
              nextData.ruleId = data.data.ruleId;
              nextData.topicTemplate = values.topicTemplate;
              nextData.topicBgUrl = values.bgImg;
              nextData.programList = data.data.programList;
              if (nextData.programList.length == 0) {
                Modal.warning({
                  title: '警告',
                  content: '所选标签对应节目列表为空，请到上个页面修改'
                });
                return;
              }
              this.handleNext(nextData);
            } else {
              // 给出错误信息
              Modal.error({
                title: '编辑主题出错',
                content: data.message + '，请到上一步修改'
              });
            }
          })
        }
      }
    });
  },

  handleNext(values) {
    this.props.onNext(2, values);
  },

  handlePrev(e) {
    e.preventDefault();
    this.props.onNext(0);
  },

  handleTemplateChange(e) {
    this.setState({
      imgIndex: e.target.value
    });
  },

  normFile(e) {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  },

  changeBgImg(img) {
    this.setState({
      bgImg: img
    });
    if (img) {
      this.setState({
        hasErr: false
      });
    }
  },

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: { span: 3 },
      wrapperCol: { span: 20 }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        span: 14,
        offset: 6
      }
    };
    let radioList = [];
    this.state.themeArr.forEach((item, index) => {
      radioList.push(<Radio key={index} value={item.value}>{item.desc}</Radio>);
    });

    return (
      <Form horizontal onSubmit={this.handleSubmit} >
        <FormItem
          {...formItemLayout}
          label="主题适用场景："
        >
          {getFieldDecorator('topicTemplate', {
            initialValue: this.state.imgIndex
          })(
              <RadioGroup onChange = {this.handleTemplateChange}>
                {radioList}
                <img className="template-img" src={this.state.themeArr[this.state.imgIndex] && this.state.themeArr[this.state.imgIndex].img} alt="模版图片"/>
              </RadioGroup>
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="上传专题背景图："
        >
          {getFieldDecorator('bgImg', {
            valuePropName: 'fileList',
            normalize: this.normFile,
            rules: [{ required: false, message: '请上传背景图片!'}]
          })(
            <UploadImg
             onImgChange={img=>this.changeBgImg(img)}
             hasErr={this.state.hasErr}
            />
          )}
        </FormItem>
        <FormItem {...tailFormItemLayout}>
          <Button onClick={this.handlePrev} size="large">上一步</Button>
          <Button type="primary" htmlType="submit" size="large">
            {formData.ruleId <= 0 ? '创建主题并生成专题' : '保存主题并生成专题'}
          </Button>
        </FormItem>
      </Form>
    );
  }

}));

export default TagSelectForm;



