'use strict';

import { Steps } from 'antd';
import './Steps.less';
const Step = Steps.Step;

class StepsComponent extends React.Component {
  render() {
    return (
      <div className="new-theme-steps">
        <Steps current={this.props.curStep} >
          <Step title="第一步:选择标签聚合数据" description="" />
          <Step title="第二步:选择模板，创建专题" description="" />
          <Step title="第三步:预览效果" description="" />
        </Steps>
      </div>
    );
  }
}

export default StepsComponent;



