/**
 * 解析后端写在页面的全局变量
 */

import globalData from '../../mock/global';

let formData = window.form || globalData;
const sceneList = formData.sceneList;
let sceneFormData = {};

/**
 * 解析树形选择器数据
 */
function getSceneDataForTreeSelect() {
  let data = sceneList;
  let treeData = [{
    label: '全部',
    value: '0-',
    key: '0-',
    children: []
  }];
  data.forEach((app) => {
    let item = {
      label: ''+app.name,
      value: ''+app.id,
      key: ''+app.id,
      children: []
    };
    sceneFormData[app.id] = [];
    if (app.data) {
      for (let key in app.data) {
        let childItem = {
          label: app.data[key],
          value: `${app.id}-${key}`,
          key: `${app.id}-${key}`
        }
        item.children.push(childItem);
        sceneFormData[app.id].push(+key);
      }
    }
    treeData[0].children.push(item);
  })
  console.log('treeData: ', treeData);
  console.log('sceneFormData: ', sceneFormData);
  return treeData;
}

const treeData = getSceneDataForTreeSelect();

export {
  treeData,
  sceneFormData
}



