'use strict';

import React from 'react';

require('../base.less');
require('./hor.less');

// mock
import mockData from '../getMock';

class HorComponent extends React.Component {

  constructor(props) {
    super(props);
  
    this.state = {};
  }

  componentDidMount() {
    // todo: use theme id to get data
    let data = mockData;
    let programList = this.props.programList;
    if (programList && programList.length > 0) {
      data = programList;
    }
    let html = this.getTemplateHtml(data);
    this.refs.temp.innerHTML += html;
    this.focusRoot = FocusEngine.render(document.body, true);
  }

  getTemplateHtml(data) {
    return `
      <div class="scroll" fe-role="Scroll" fe-cfg="scroll_dir:h,scroll_duration:0.3,scroll_easing:ease-out,disable_lazyload:yes,auto_list_width:yes,scroll_right_padding:50px">
        <div class="scroll-list">
          ${
            data && data.map((item, index) => {
              return `<div class="temp-item" fe-role="Widget">
                        <img class="poster" src="${item.picUrl}">
                        <span class="title">${item.name}</span>
                      </div>`
            }).join('')
          }
        </div>
      </div>
    `;
  }

  render() {
    const {bgImg} = this.props;
    return (
      <div ref="temp" className="hor-component pre-template">
        <img className="bg-img" src={bgImg} alt=""/>
      </div>
    );
  }

}

HorComponent.displayName = 'HorComponent';

export default HorComponent;



