'use strict';

import './TagSelectForm.less';
import { Form, Input, Radio, Switch, Button, Select, TreeSelect, Modal} from 'antd';
import TagSelectTransfer from '../tagSelectTransfer/TagSelectTransferComponent';
import UpDownload from '../upDownload/UpDownload';
import {treeData, sceneFormData} from '../globalData/globalData';
import globalData from '../../mock/global';
import RequestManager from 'components/request-manager/request-manager';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const SHOW_PARENT = TreeSelect.SHOW_PARENT;

const formData = window.form || globalData;

const TagSelectForm = Form.create()(React.createClass({

  getInitialState() {
    let sceneMap = this.createSceneMap(formData.sceneMap);
    return {
      includeProgramIds: [],
      excludeProgramIds: [],
      tagIds: [],
      ...formData,
      sceneMap
    };
  },

  createSceneMap(data) {
    if (!data) return [];
    let sceneMap = [];
    for (let key in data) {
      let item = data[key];
      if (item.length == 0) {
        sceneMap.push(''+key);
      } else {
        item.forEach((val) => {
          let scene = `${key}-${val}`;
          sceneMap.push(scene);
        });
      }
    }
    return sceneMap;
  },

  componentDidMount() {
    // todo: 如果是编辑页面，需要根据已有数据渲染出该页面

  },

  /**
   * 点击创建主题
   */
  handleSubmit(e) {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values = this.postProcessFormData(values);
        console.log('Received values of form: ', values);
        // 创建主题 post, 如果是编辑，是另一个接口
        if (values.ruleId <= 0) {
          RequestManager.postNewTheme(values)
          .then(data => {
            console.log('post new theme data return: ', data);
            if (data.success == true) {
              location.href = '/topic/rule/index.htm';
            } else {
              // 给出错误信息
              Modal.error({
                title: '新建主题出错',
                content: data.message
              });
            }
          })
        } else {
          // 编辑的 post 接口
          RequestManager.postEditTheme(values)
          .then(data => {
            console.log('post edit theme data return: ', data);
            console.log(JSON.stringify(data));
            if (data.success == true) {
              location.href = '/topic/rule/index.htm';
            } else {
              // 给出错误信息
              Modal.error({
                title: '编辑主题出错',
                content: data.message
              });
            }
          })
        }
      }
    });
  },

  /**
   * 点击下一步
   */
  handleNext(e) {
    e.preventDefault();
    if (this.state.topicId <= 0) {
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          values = this.postProcessFormData(values);
          console.log('Received values of form: ', values);
          this.props.onNext(1, values);
        }
      });
    } else {
      // todo: 查看专题预览, test
      this.props.onNext(2, {
        hasTopic: true,
        topicTemplate: '1',
        topicBgUrl: '',
        programList: []
      });
    }
  },

  /**
   * form 数据后处理
   */
  postProcessFormData(values) {
    let postFormData = values;
    let sceneMap = values.sceneMap;
    // 处理主题适用场景提交数据，转换为后端所需格式
    let sceneData = {};
    console.log('sceneMap: ', sceneMap);
    sceneMap.forEach((item, index) => {
      let itemArr = item.split('-');
      let key = itemArr[0];
      let value = itemArr[1];
      if (key == 0) {
        sceneData = sceneFormData;
      } else {
        sceneData[key] = sceneData[key] || [];
        if (value) {
          sceneData[key].push(+value);
        } else {
          sceneData[key] = sceneFormData[key];
        }
      }
    });
    postFormData.sceneMap = sceneData;
    // true to 1
    postFormData.syncStatus = values.syncStatus === true ? '1' : '0';
    postFormData.ruleStatus = values.ruleStatus === true ? '1' : '0';
    postFormData.ruleId = formData.ruleId+'';
    postFormData.topicId = formData.topicId+'';
    return postFormData;
  },

  /**
   * 处理穿梭框内容变化
   */
  onTransferChange(data) {
    console.log('transfer data: ', data);
    this.props.form.setFieldsValue({
      tagIds: data.tagIds,
      policyType: data.policyType
    });
    // todo: 更新预览框内容

  },

  /**
   * 处理上传excel文件
   */
  onUploadInclude(data) {
    let includeProgramIds = this.props.form.getFieldsValue(['includeProgramIds']).includeProgramIds;
    this.props.form.setFieldsValue({
      includeProgramIds: this.uniqueArr(includeProgramIds.concat(data))
    });
  },
  onUploadExclude(data) {
    let excludeProgramIds = this.props.form.getFieldsValue(['excludeProgramIds']).excludeProgramIds;
    this.props.form.setFieldsValue({
      excludeProgramIds: this.uniqueArr(excludeProgramIds.concat(data))
    });
  },

  /**
   * 数组去重
   */
  uniqueArr(array) {
    let n = [];
    for (var i = 0; i < array.length; i++) {
      if (n.indexOf(array[i]) == -1) n.push(array[i]);
    }
    return n;
  },

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: { span: 3 },
      wrapperCol: { span: 20 }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        span: 14,
        offset: 6
      }
    };
    const upDownloadLayout = {
      wrapperCol: {
        span: 14,
        offset: 3
      }
    }
    let tProps = {
      treeData,
      multiple: true,
      treeCheckable: true,
      showCheckedStrategy: SHOW_PARENT,
      treeDefaultExpandAll: true,
      searchPlaceholder: '请点击选择'
    };
    let transferProps = {
      tagIds: this.state.tagIds,
      tagMap: this.state.tagMap,
      policyType: this.state.policyType,
      policyTypeMap: this.state.policyTypeMap,
      onTargetChange: this.onTransferChange
    };
    let {dataTypeMap} = this.state;
    let dataTypeList = [];
    for(let key in dataTypeMap) {
      if (key == 1) { // 专题目前暂时不支持
        dataTypeList.push(
          <Radio key={key} value={key} disabled>
            {dataTypeMap[key]}
            <span style={{color:'#f50'}}>&nbsp;&nbsp;&nbsp;目前只支持节目的聚合规则，稍后会开放专题的聚合规则</span>
          </Radio>);
      } else {
        dataTypeList.push(<Radio key={key} value={key}>{dataTypeMap[key]}</Radio>);
      }
    }

    return (
      <Form horizontal onSubmit={this.handleSubmit}>
        <FormItem
          {...formItemLayout}
          label="主题名称："
          hasFeedback
        >
          {getFieldDecorator('ruleName', {
            initialValue: formData.ruleName,
            rules: [{required: true, message: '请输入主题名称！'}]
          })(
            <Input />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="主题适用场景："
        >
          {getFieldDecorator('sceneMap', {
            initialValue: this.state.sceneMap
          })(
            <TreeSelect {...tProps} />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="数据类型："
        >
          {getFieldDecorator('dataType', {
            initialValue: this.state.dataType+''
          })(
            <RadioGroup>
              {dataTypeList}
            </RadioGroup>
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="选取主题标签："
        >
          {getFieldDecorator('tagIds', {

          })(
            <TagSelectTransfer {...transferProps} />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="指定包含："
        >
          {getFieldDecorator('includeProgramIds', {
            initialValue: this.state.includeProgramIds
          })(
            <Select tags
              style={{ width: '100%' }}
              tokenSeparators={[',']}
            >
            </Select>
          )}
        </FormItem>
        <FormItem {...upDownloadLayout} className="toup">
          <UpDownload onUpload={this.onUploadInclude} />
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="指定排除："
        >
          {getFieldDecorator('excludeProgramIds', {
            initialValue: this.state.excludeProgramIds
          })(
            <Select tags
              style={{ width: '100%' }}
              tokenSeparators={[',']}
            >
            </Select>
          )}
        </FormItem>
        <FormItem {...upDownloadLayout} className="toup">
          <UpDownload onUpload={this.onUploadExclude} />
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="标签聚合生效："
        >
          {getFieldDecorator('syncStatus', {
            valuePropName: 'checked',
            initialValue: formData.syncStatus == 1 ? true : false
          })(
            <Switch checkedChildren="开" unCheckedChildren="关" />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="主题状态："
        >
          {getFieldDecorator('ruleStatus', {
            valuePropName: 'checked',
            initialValue: formData.ruleStatus == 1 ? true : false
          })(
            <Switch checkedChildren="上线" unCheckedChildren="下线" />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label="主题描述："
          hasFeedback
        >
          {getFieldDecorator('descrip', {
            initialValue: formData.descrip
          })(
            <Input type="textarea" rows={4} />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('policyType', {
            initialValue: formData.policyType
          })(
              <Input className="hide" />
          )}
        </FormItem>
        <FormItem {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit" size="large">
            {this.state.ruleId <= 0 ? '创建主题' : '保存'}
          </Button>
          <Button onClick={this.handleNext} size="large">
            {this.state.topicId <= 0 ? '下一步' : '查看专题'}
          </Button>
        </FormItem>
      </Form>
    );
  }
}));

export default TagSelectForm;



