'use strict';

import './TagSelectTransfer.less';
import { Transfer, Radio } from 'antd';
const RadioGroup = Radio.Group;

const TagSelectTransfer = React.createClass({
  getInitialState() {
    return {
      sourceData: [],
      targetKeys: [],
      radioVal: this.props.policyType+'',
      mockSelectData: '敬请期待功能上线'.split('')
    };
  },

  componentDidMount() {
    // todo: 请求主题相关标签列表
    this.getData();
  },

  getData() {
    const targetKeys = [];
    const sourceData = [];
    let {tagMap, tagIds} = this.props;
    for (let key in tagMap) {
      const data = {
        key: key,
        title: `${key}`,
        description: tagMap[key],
        chosen: tagIds.indexOf(+key) != -1
      };
      if (data.chosen) {
        targetKeys.push(data.key);
      }
      sourceData.push(data);
    }
    this.setState({ sourceData, targetKeys });
    this.props.onTargetChange({tagIds : targetKeys, policyType: this.state.radioVal});
  },

  handleChange(targetKeys) {
    this.setState({ targetKeys });
    this.props.onTargetChange({tagIds : targetKeys, policyType: this.state.radioVal});
  },

  onRadioChange(e) {
    this.setState({
      radioVal: e.target.value
    });
    this.props.onTargetChange({tagIds : this.state.targetKeys, policyType: e.target.value});
  },

  renderFooter() {
    let radioList = [];
    let {policyTypeMap} = this.props;
    for(let key in policyTypeMap) {
      radioList.push(<Radio key={key} value={key}>{policyTypeMap[key]}</Radio>);
    }
    return (
      <div className="tag-select-transfer-footer">
        <div className="footer-title">标签之间的条件</div>
        <RadioGroup onChange={this.onRadioChange} value={this.state.radioVal}>
          {radioList}
        </RadioGroup>
      </div>
    );
  },

  render() {
    let dataList = this.state.mockSelectData.map((item, index) => {
      return (
        <li className="list-preview-content-item" key={index}>
          {item}
        </li>
      );
    });

    return (
      <div className="tag-select-transfer">
        <Transfer
          dataSource={this.state.sourceData}
          titles={['待选标签', '已选标签']}
          showSearch
          listStyle={{
            width: 250,
            height: 400
          }}
          footer={this.renderFooter}
          targetKeys={this.state.targetKeys}
          onChange={this.handleChange}
          render={item => `${item.description}`}
        />
        <div className="list-preview">
          <div className="list-preview-header">
            <span className="target-data">圈中<i> x </i>条节目</span>
            <span className="desc">预览列表</span>
          </div>
          <div className="list-preview-body">
            <ul className="list-preview-content">
              {dataList}
            </ul>
          </div>
        </div>
      </div>
    );
  }

});

export default TagSelectTransfer;



